
body
{
   
   align-items: center;
   justify-content: center;
   display: flex;
}
.container
{
  top: 30%;
  left:50%;
  position: absolute;
  text-align: center;
  border: 10px solid rgb(53, 42, 53);
  width: 300px;
  border-radius: 20px;
  background-color: rgb(20, 2, 2);
 
}
.keyboard
{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-flow: minmax(60px,auto);

}
input{
  height: 75px;
  width: 290px;
  background-color: rgb(46, 53, 59);
  color: white;
  text-align: right;
  font-size: 35px;
  font-weight: 900;
  border-radius: 10px;

}
.clear
{
  color: red;
  font-weight: 800;
  grid-column: 1/3;
  grid-row: 1;
}
.backspace
{
  color: red;
}
#highlight
{
  background-color: rgb(135, 119, 241);
  font-weight: 900;
  font-size: larger;
  text-align: center;
  

}
.equal
{
  grid-column: 3/5;
  grid-row: 5;
  color: red;
}
button
{
  margin: 10px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-weight: 900;
  font-size: larger;
  height: 55px;
}
